  overflow: auto;
  line-height: 1.7;
word-spacing: 0.5em;
  margin-left: -16.5em;
  background: white;
  color: #0f0f0f;
  }

  
.modal:-webkit-scrollbar-track {
    
  border-radius: 10px;
  background-color:transparent;
}

.phone-number::-webkit-scrollbar {
  width: 6px;
  background-color: transparent;
  border-radius: 10px;
}

.phone-number::-webkit-scrollbar-thumb {
  border-radius: 10px;
 
  background-color:#434343;
}


             
.callus:hover .phone-number {
  display: block;
  }

